.cookie-banner {
  position: fixed;
  bottom: 0;
  background-color: #707070;
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  z-index: 9700;
  flex-wrap: wrap;
  text-align: center;
}
.cookie-banner .q-btn {
  margin: 0;
  margin-left: 5px;
}
@media only screen and (min-width: 0px) and (max-width: 719px) {
  .cookie-banner {
    min-height: 50px;
    height: auto;
  }
  .cookie-banner .q-btn {
    margin-top: 5px;
    margin-bottom: 5px;
  }
}
